  --a-padding--x2: 0.5rem;
  --a-padding--x3: 0.75rem;
  --a-padding--x4: 1rem;
  --a-padding--x5: 1.25rem;
  --a-padding--x6: 1.5rem;
  --a-padding--x7: 1.75rem;
  --a-padding--x8: 2rem;
  --a-padding--x9: 2.25rem;
  --a-padding--x10: 2.5rem;
  --a-padding--x11: 2.75rem;
  --a-padding--x12: 3rem;
  --a-padding--x13: 3.25rem;
  --a-padding--x14: 3.5rem;
  --a-padding--x15: 3.75rem;
  --a-padding--x16: 4rem;
  --a-padding--x17: 4.25rem;
  --a-padding--x18: 4.5rem;
  --a-padding--x19: 4.75rem;
  --a-padding--x20: 5rem;
}

:root {
  --a-borderRadius--x1: 0.25rem;
  --a-borderRadius--x2: 0.5rem;
  --a-borderRadius--x3: 0.75rem;
  --a-borderRadius--x4: 1rem;
  --a-borderRadius--x5: 1.25rem;
  --a-borderRadius--x6: 1.5rem;
  --a-borderRadius--x7: 1.75rem;
  --a-borderRadius--x8: 2rem;
  --a-borderRadius--x9: 2.25rem;
  --a-borderRadius--x10: 2.5rem;
  --a-borderRadius--x11: 2.75rem;
  --a-borderRadius--x12: 3rem;
  --a-borderRadius--x13: 3.25rem;
  --a-borderRadius--x14: 3.5rem;
  --a-borderRadius--x15: 3.75rem;
  --a-borderRadius--x16: 4rem;
  --a-borderRadius--x17: 4.25rem;
  --a-borderRadius--x18: 4.5rem;
  --a-borderRadius--x19: 4.75rem;
  --a-borderRadius--x20: 5rem;
  --a-borderRadius--x21: 5.25rem;
  --a-borderRadius--x22: 5.5rem;
  --a-borderRadius--x23: 5.75rem;
  --a-borderRadius--x24: 6rem;
  --a-borderRadius--x25: 6.25rem;
  --a-borderRadius--def: var(--a-borderRadius--x4);
}

.font-h1 {
  font-family: 'Lora';
  font-size: 1.5rem;
  font-weight: 500;
  line-height: 1.5rem;
}

.font-h2 {
  font-family: 'Lora';
  font-size: 1.375rem;
  font-weight: 500;
  line-height: 1.5rem;
}

.font-h3 {
  font-family: 'Lora';
  font-size: 1.25rem;
  font-weight: 500;
  line-height: 1.5rem;
}

.font-h4 {
  font-family: 'Lora';
  font-size: 1.125rem;
  font-weight: 500;
  line-height: 1.375rem;
}

.font-xl {
  font-family: 'Lora';
  font-size: 1rem;
  line-height: 1.25rem;
}

.font-l {
  font-size: 0.875rem;
  line-height: 1.125rem;
}

.font-m {
  font-size: 0.75rem;
  line-height: 1rem;
}

.font-s {
  font-size: 0.625rem;
  line-height: 0.875rem;
}

.font-xs {
  font-size: 0.875rem;
  line-height: 1.25rem;
}

.font-button {
  font-size: 0.75rem;
  line-height: 0.875rem;
}

.font-menu {
  font-family: 'Lora';
  font-size: 0.75rem;
  line-height: 1rem;
}

.font-bold {
  font-weight: 700;
}

.font-semibold {
  font-weight: 600;
}

.font-medium {
  font-weight: 500;
}

.font-regular {
  font-weight: 400;
}

.font-light {
  font-weight: 300;
}

.s-page-contacts__inner {
  display: flex;
  gap: 3.5rem;
  flex-wrap: wrap;
}

.s-page-contacts__information {
  display: flex;
  width: 100%;
  flex-direction: column;
  gap: 1rem;
  flex: none;
}

.s-page-contacts__form {
  min-width: 0;
}

.s-page-contacts__item {
  width: 100%;
  background-color: var(--color-white);
}

.s-page-contacts__item.is-open .s-page-contacts__item-content {
  max-height: 100%;
  padding: 1.5rem 1rem;
  opacity: 1;
}

.s-page-contacts__item.is-open .s-page-contacts__item-map {
  opacity: 1;
  max-height: 100%;
}

.s-page-contacts__item-header {
  display: grid;
  gap: 0;
  grid-template-columns: repeat(2, 1fr);
}

.s-page-contacts__item-photo {
  position: relative;
}

.s-page-contacts__item-photo img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.s-page-contacts__item-info {
  display: flex;
  justify-content: space-between;
  padding: 1rem;
  flex-direction: column;
  gap: 1.5rem;
}

.s-page-contacts__item-title {
  color: var(--color-dark-blue);
}

.s-page-contacts__item-toggle {
  cursor: pointer;
  position: relative;
  width: fit-content;
  padding-left: 1rem;
  color: var(--color-dark-gold);
  border: 0;
  background-color: transparent;
}

.s-page-contacts__item-toggle::before {
  position: absolute;
  top: 50%;
  left: 0;
  width: 0.75rem;
  height: 0.0625rem;
  transform: translateY(-50%);
  content: '';
  background-color: var(--color-dark-gold);
}

.s-page-contacts__item-content,
.s-page-contacts__item-map {
  overflow: hidden;
  max-height: 0;
  opacity: 0;
  transition: max-height 0.1s ease-in-out;
}

.s-page-contacts__item-content {
  display: grid;
  gap: 1.5rem;
  grid-template-columns: repeat(1, 1fr);
}

.s-page-contacts__line {
  display: flex;
  align-items: flex-start;
  gap: 0.75rem;
}

.s-page-contacts__line-icon {
  width: 1.25rem;
  flex: none;
}

.s-page-contacts__line-icon svg {
  width: 100%;
  height: auto;
}

.s-page-contacts__line-info span {
  display: block;
  color: var(--color-black);
}

.s-page-contacts__line-info a {
  cursor: pointer;
  transition-duration: 0.4s;
}

.s-page-contacts__line-info a:hover {
  color: var(--color-dark-gold);
}

.s-page-contacts__line-download {
  display: block;
  margin-top: 1.5rem;
  font-weight: 400;
  color: var(--color-dark-blue);
}

@media (min-width: 768px) {
  .s-page-contacts__item-toggle {
    padding-left: 1.75rem;
  }

  .s-page-contacts__item-toggle::before {
    width: 1.25rem;
  }
}

@media (min-width: 992px) {
  .s-page-contacts__inner {
    justify-content: space-between;
    flex-wrap: nowrap;
  }

  .s-page-contacts__information {
    width: 50%;
    gap: 2.5rem;
  }
}

@media screen and (min-width: 1024px) {
  .font-h1 {
    font-size: 4.75rem;
    font-weight: 400;
    line-height: 5.5rem;
  }

  .font-h2 {
    font-family: 'Lora';
    font-size: 3rem;
    line-height: 3.5rem;
  }

  .font-h3 {
    font-size: 2.625rem;
    line-height: 3.25rem;
  }

  .font-h4 {
    font-size: 2.5rem;
    font-weight: 400;
    line-height: 3rem;
  }

  .font-xl {
    font-size: 1.875rem;
    line-height: 2.5rem;
  }

  .font-l {
    font-size: 1.5rem;
    line-height: 2rem;
  }

  .font-m {
    font-size: 1.25rem;
    line-height: 1.75rem;
  }

  .font-s {
    font-size: 1rem;
    line-height: 1.375rem;
  }

  .font-xs {
    font-size: 0.875rem;
    line-height: 1.25rem;
  }

  .font-button {
    font-size: 1rem;
    line-height: 1.25rem;
  }

  .font-menu {
    font-size: 1rem;
    line-height: 1.375rem;
  }
}

@media (min-width: 1024px) {
  .s-page-contacts__item.is-open .s-page-contacts__item-content {
    padding: 3rem;
  }

  .s-page-contacts__line {
    gap: 0.875rem;
  }

  .s-page-contacts__line-icon {
    width: 1.75rem;
  }
}

@media (min-width: 1440px) {
  .s-page-contacts__item-info {
    padding: 2rem 2.5rem;
    gap: 4.375rem;
  }
}

